@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: bold;
    margin: 0px 0px 0px -10px;
    padding-left: 7%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: absolute;
    z-index: 100;
    width: 92.5%;
    top: 0;
}

.topNav {
    float: right;
    padding: 0% 5% 0% 0%;
    font-size: 13px;
}
  
.navBar a:hover {
    color: white;
}

.dropNav {
    float: right;
    overflow: hidden;
    display: none;
    margin-right: 2vw;
}

#dropbtn {
    background-color: transparent;
    font-size: 25px;
    /* border: 2px solid black;
    letter-spacing: .15em;
    text-transform: uppercase;
    font-weight: bold;
    padding: 7px 10px; */
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: transparent;
    min-width: 160px;
    z-index: 1;
    text-align: right;
}

.dropNav:hover .dropdown-content {
    display: block;
    margin-left: -130px;
}

.min-screen {
    float: none;
    color: black;
    font-weight: normal;
    padding: 5px 0px;
    text-decoration: none;
    display: block;
    text-align: right;
    font-size: 13px;
}

a:link, a:visited, a:active {
    color: black;
    text-decoration: none;
  }


@media screen and (max-width: 800px) {
    .dropNav {
        display: block;
    }
    
    .topNav {
        display: none;
    }
  }
.footer {
    font-size: 25px;
    width: 98%;
    margin-left: 2%;
    font-style: italic;
    position: fixed;
    letter-spacing: .25em;
    /* background-color: white; */
    bottom: 0;
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
  }

#footer-right {
  float: right;
  width: 20%;
}
  
.linkedin, .github {
  padding: 7px;
}

.email {
  padding: 0px 7% 0px 7px;
}

.credits {
  display: none;
}

@media screen and (max-width: 800px) {
  .footer {
    position: relative;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f79f99;
    height: 125px;
    width: 99%;
    margin-top: -5%;
    padding-left: 1%;
    margin-left: -.05%;
  }
  
  .credits {
    display: block;
    font-size: 2vw;
    letter-spacing: normal;
    padding-top: 25px;
    font-style: normal;
  }

  #footer-right {
    width: 100%;
    margin-left: 5%;
  }
}

@media screen and (max-width: 450px) {
  .footer {
    height: 75px;
  }

  .credits {
    padding-top: 15px;
  }
}
#about {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    margin: 115px 10%;
    height: 100vh;
}

h2 {
    letter-spacing: .075em;
}

.skills {
    display: flex;
    flex-direction: column;
    width: 35%;
    display: block;
}

.skills img {
    display: right;
    height: 65%;
    width: auto;
}

.summary {
    width: 40%;
    text-align: left;
    line-height: 1.5em;
    text-align: justify;
}



@media screen and (max-width: 1000px) {
    #about {
        display: flex; 
        flex-direction: column;
        justify-content: space-evenly;
        margin: 15% 5%;
        align-items: center;
    }
    .skills img {
        margin: 15vh 0 5vh -19vw;
        height: 375px;
        width: 70vw;
        object-fit: cover;
    }
    .summary {
        width: 80%;
        margin-bottom: 25vh;
    }
}

@media screen and (max-width: 800px) {
    #about {
        margin-top: -25%;
    }
    .skills img {
        margin: -55vh 0 -35vh -19vw;
    }
    .summary {
        margin-top: -17%;
        margin-bottom: -95vh;
    }
}

@media screen and (max-device-height: 825px) {
    #about {
        margin-top: -30vh;
    }
}

@media screen and (max-device-height: 750px) {
    #about {
        margin-top: -23vh;
    }
}

@media screen and (max-device-height: 670px) {
    #about {
        margin-top: -15vh;
    }
}
#projects {
    background: #f7f7f7;
    max-height: 100vh;
    width: 100% !important;
    overflow-x: hidden !important;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.project-title {
    font-weight: bold;
    text-align: center;
    font-size: 3.75vw;
    letter-spacing: .17em;
    padding: 12vh 0px 0px 0px;
    display: none;
}

.first-row-project {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  margin: 2.5%;
  min-height: 55vh;
}
  
#projects img {
    width: 100%;
    height: auto;
    object-fit: cover;
}



/* FOR TESTING PURPOSES */
.full-page-projs {
    float: right;
    width: 50%;
    overflow-y: scroll;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
}

.full-page-projs::-webkit-scrollbar {
    display: none;
}

.child {
    scroll-snap-align: center;
}

#proj1-img, #proj2-img, #proj3-img {
    opacity: .25;
}

#proj3-img {
    margin-bottom: -10px;
}

#try, #skin, #pic {
    display: none;
}

.proj-name span{
    font-size: 3vw;
    font-weight: bold;
    color: #f79f99;
}

.projects-right {
    float: left;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: 18vh 5vw;
    width: 30%
}

