/* TESTING PURPOSES */




@media screen and (max-width: 1000px) {
    .first-row-project {
        display: flex; 
        flex-direction: column;
        justify-content: space-evenly;
        margin: 5%;
    }
    #projects {
        max-height: none;
        padding-bottom: 5%;
    }
    .project-title {
        display: block;
    }
    #projects img {
        padding: 2.5% 0px;
        opacity: 1;
    }
    .projects-right {
        display: none;
    }
    .full-page-projs {
        width: 80%;
        overflow-y: hidden;
        padding: 0% 15%;
    }
}

@media screen and (max-width: 600px) {
    #projects img {
        width: 400px;
        height: 250px; 
        object-fit: cover;
        margin: 0px 3vw 3vh 3vw ;
    }
}

@media screen and (max-width: 450px) {
    #projects img {
        width: 100%;
        height: 50%;
        object-fit: cover;
        margin: 0px 3vw 3vh -1vw;
    }
}


#contact-me { 
    background: linear-gradient(35deg, #ffffff 65%, #f79f99 45%);
    position: absolute;
    width: 100% !important;
    overflow-x: hidden !important;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.helloText {
  margin-top: 20vh;
}

.helloText h1 {
  font-size: 4.5vw;
}

.helloText p {
  font-size: 1.5vw;
}

.sayHello {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1%;
    font-size: 1.5vw;
    margin-left: -75px;
    margin-top: 50px;
}


@media screen and (min-width: 1100px) {
  .sayHello {
     font-size: 17px;
  }
}

@media screen and (max-width: 800px) {
  #contact-me { 
    height: 75vh;
  }
  .helloText {
    margin-top: 20vh;
  }
  .helloText h1 {
    font-size: 7vw;
  }
  .helloText p {
    font-size: 2vw;
  }
  .sayHello {
    margin-left: -10%;
 }
}


#welcome-page {
  min-height: 100vh;
  width: 100% !important;
  overflow-x: hidden !important;
}

.welcomeHeader {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 5vw;
    text-transform: lowercase;
    font-weight: bold;
    letter-spacing: .15em;
}
  
.typeWrite {
  font-family: 'Lexend Deca';
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(40, end);
          animation: 
    typing 3.5s steps(40, end);
}
  
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
  
.top {
    background-color: #f79f99;
    padding: 10% 0% .5% 0%;
    justify-content: center;
    display: flex;
    height: 50%;
}
  
.bottom {
    padding: 0% 25% 0% 10%;
    justify-content: center;
    align-items: center;
    display: flex;
}
  
.exploreButton {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1.1%;
    font-size: 2vw;
}




@media screen and (min-width: 1100px) {
  .exploreButton {
     font-size: 17px;
  }
}

@media screen and (max-width: 800px) {
  .bottom {
    padding: 0% 25vw 0% 7vw;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  #welcome-page {
    background-color: #f79f99;
    padding: 25vh 0vw 0vh 11vw;
    justify-content: left;
    display: flex;
    min-height: 75vh;
    width: 89vw !important;
  }

  .typeWrite {
    font-size: 8vw;
  }
}

@media screen and (max-width: 450px) {
  #welcome-page {
    padding: 25vh 5.5vw 0vh 5.5vw;
  }
}

